.button {
  background: linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);
  background-blend-mode: darken;
  border-radius: 8px;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  text-decoration: none;
  padding: 12px;
}
.button:hover {
  box-shadow: inset 0 0 100px 100px rgba(0, 0, 0, 0.1);
}
.button:active {
  box-shadow: inset 0 0 100px 100px rgba(0, 0, 0, 0.2);
}

.slider-item__btn {
  width: 164px;
  height: 48px;
  margin: 0 96px;
  margin-top: 32px;
  border-radius: 4px;
}

.btn1 {
  background: linear-gradient(90deg, hsl(169, 59%, 18%) 0%, hsl(128, 82%, 26%) 100%);
}
.btn2 {
  background: linear-gradient(90deg, hsl(216, 59%, 18%) 0%, hsl(169, 82%, 26%) 100%);
}
.btn3 {
  background: linear-gradient(90deg, hsl(32, 59%, 18%) 0%, hsl(335, 82%, 26%) 100%);
}
.btn4 {
  background: linear-gradient(90deg, hsl(263, 59%, 18%) 0%, hsl(295, 82%, 26%) 100%);
}

@media screen and (max-width: 1439px) {
  .slider-item__btn {
    margin: 32px 64px;
  }
}
@media screen and (max-width: 767px) {
  .hero-block__btn {
    width: 100%;
    border-radius: 0%;
  }
}

.button--disabled {
  background: #eeeeee;
}

.status__price-btn {
  width: 100%;
}

.button--cancel {
  background-image: linear-gradient(90deg, #493013 0%, #7B0C3B 100%);
}
